import * as React from 'react';
import { DataGrid } from '@mui/x-data-grid';
import fetchTransactions from '../scripts/api/fetchTransactions';
import { useState, useEffect } from 'react';

const columns = [
    { field: 'id', headerName: 'ID', width: 70},
    { field: 'nominal', headerName: '', width: 80},
    { field: 'amount', headerName: '', width: 70},
    { field: 'destination', headerName: '', width: 100},
    { field: 'source', headerName: '', width: 100},
    { field: 'description', headerName: '', width: 200, flex: 0.3},
];

export default function DataTable() {
    const [rows, setRows] = useState([]);
    
    useEffect(() => {
        const get = async() => {
            const data = await fetchTransactions();

            setRows(data);
        };
        get();
    }, [])
    
    return (
        <div style={{ height: 400, width: '60%'}}>
            <DataGrid
                rows={rows}
                columns={columns}
            />
        </div>
    );
}